.btnModal button{
    display: inline-block;
    background-color: #ff0000;
    color :#ffffff;
    padding: 10px 20px;
    float: right;
    border: 2px solid #000000;
    box-shadow: #faebcc;
}

.btnModal{
    padding: 50px;
}

.loginBtn {
    background-color: #c7cfff;
    color: #ffffff;
    margin-right: auto;
    padding: 12px 16px;
    border: none;
    cursor: pointer;
    opacity: 0.9;
    font-size: 15px;
    /*box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);*/
}
.loginContainer {

    top: 30px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
    width: 400px;
    padding: 16px;
    background-color: #ffffff;
    box-shadow: 0 10px 18px 0 rgba(0,0,0,0.2);
}
.fa-user{
    font-size:130%;
    color: #1f2a9e;
}
.fa-lock {
    font-size:150%;
    color: #1f2a9e;
}

.fa-mobile-phone{
    color: #1f2a9e;
    font-size:200%;
}
.loginContainer input[type="text"],
.loginContainer input[type="password"] {

    width: 85%;
    padding: 15px;
    margin: 5px 0 22px 0;
    border: none;
    background: #f1f1f1;
    /*box-shadow: 0 1px 2px rgba(0, 0, 0, 0.4);*/
    /*display: block;
    box-sizing: border-box;
    margin-bottom: 10px;
    padding: 8px;
    width: 100%;
    border: none;
    border-bottom: 2px solid #6e6e6e;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    transition: 0.2s ease;*/
}

.loginContainer input[type="text"]:focus,
.loginContainer input[type="password"]:focus {
    border-bottom: 2px solid #c7cfff;
    color: #000000;
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
}

.loginContainer button[type="submit"] {
    margin-top: 2px;
   alignment: center;
    margin-left: 40%;
    background:#152a9e;
    border: 0;
    font-size: 19px;
    border-radius: 1px;
    color: #ffffff;
    font-family: Arial, Helvetica, sans-serif;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
    -webkit-transition: 0.1s ease;
    transition: 0.1s ease;
    cursor: pointer;
}

.loginContainer input[type="submit"]:hover,
.loginContainer input[type="submit"]:focus {
    opacity: 0.8;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.4);
    -webkit-transition: 0.1s ease;
    transition: 0.1s ease;
}

.loginContainer input[type="submit"]:active {
    opacity: 1;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.4);
    -webkit-transition: 0.1s ease;
    transition: 0.1s ease;
}


