.language {
    position: relative;
    float: right;
    margin-right: 10%;
}

.language-content {
    float: none;
    width: 100%;
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
.language-content ul{
    list-style-type: none;
    float: left;
}
.language-content a{
    display: block;
    color: black;
    text-align: left;
    padding: 12px 16px;
}

.language-content a:hover {
    background-color: #f1f1f1;
    cursor:pointer;
}

.language:hover .language-content {
    display: block;
}

.language:hover .dropbtn {
    background-color: #238e82;
